// ,render,renderrender
export default {
    name: 'render-cell',
    functional: true, //  ( data )  ( this ) render 
    props: {
        render: {
            type: Function,
            default () {
                return function () {}
            }
        }
    },
    render (h, ctx) {
        return ctx.props.render(h)
    }
}
