<template>
    <div class="zov-popper">
        <slot></slot>
        <span v-if="!this.noArrow" class="zov-popper-arrow"></span>
    </div>
</template>
<script>
import Popper from 'popper.js'
let prefix = 'zov-popper'
export default {
    name: prefix,
    props: {
        value: {
            type: Boolean,
            default: false
        },
        placement: {
            type: String,
            default: 'bottom'
        },
        fix: {
            type: Boolean,
            default: true
        },
        noArrow: {
            type: Boolean,
            default: false
        },
        size: {
            // 
            type: String,
            default: ''
        }
    },
    data () {
        return {
            popper: null
        }
    },
    watch: {
        size (val) {
            this.popper.update()
        }
    },
    methods: {
        // csspopperorigin
        resetTransfromOrigin (origin) {
            let transObj = {
                'right': 'left',
                'left': 'right',
                'bottom': 'top',
                'top': 'bottom'
            }
            this.popper.popper.style.transformOrigin = transObj[origin]
        },
        createPopper () {
            let _this = this
            _this.popper = new Popper(
                _this.$el.parentNode,
                _this.$el,
                {
                    placement: _this.placement, // 'bottom'
                    positionFixed: _this.fix, // position: fixedfalse
                    // eventsEnabled: true, // true
                    // removeOnDestroy: false, // destroypopperfalse
                    onCreate: (data) => {
                        this.resetTransfromOrigin(data.placement)
                    },
                    onUpdate: (data) => {
                        this.resetTransfromOrigin(data.placement)
                    },
                    // 
                    modifiers: {
                        arrow: {
                            element: '.zov-popper-arrow'
                        },
                        computeStyle: {
                            gpuAcceleration: false // CSS 3Dpopper
                        },
                        flip: {
                            // behavior: 'clockwise' // 'flip'
                        },
                        preventOverflow: {
                            padding: 0,
                            boundariesElement: 'window' //  'window' 'viewport' scrollParent
                        }
                    }
                }
            )
        }
    },
    mounted () {
        this.createPopper()
    },
    destroyed () {
        this.popper.destroy()
    },
    updated () {
        this.popper.update()
    }
}
</script>
